import React from 'react'
import ReactDOM from 'react-dom'
import './../index.css'
import App from './../App'
/* eslint-disable */
import store from './store.js'
/* eslint-disable */


function renderAll(store){
	ReactDOM.render(
	  <React.StrictMode>
	    <App state={ store.state } fn={ store.fn } />
	  </React.StrictMode>,
	  document.getElementById('root')
	)
}

export default renderAll