import React from 'react'
import { BrowserRouter } from 'react-router-dom'
import Header from './components/header/Header'
import Main from './components/main/Main'
import Footer from './components/footer/Footer'


function App({state, fn}){

	let projects, menu, copyright
	state.bzProjects 	? projects = state.bzProjects	: projects = false
	state.menu			? menu = state.menu 			: menu = false
	state.copyright 	? copyright = state.copyright 	: copyright = false

	return(
		<div className="container">
		<BrowserRouter>
			<header>
				<Header project={ projects[0] } menu={ menu } fn={ fn } />
			</header>

			<main>
				<Main state={ state } fn={ fn } />
			</main>

			<footer>
				<Footer projects={ projects } copyright={ copyright } />
			</footer>
		</BrowserRouter>
		</div>
	)
}

export default App;
