import renderAll from './renderAll.js'
import $ from 'jquery'

let API = 'https://api.bzdrive.com'
// let API = 'http://localhost:5000'

/* eslint-disable */
const files = "http://files.bzdrive.com/img/"
/* eslint-disable */

function getAuth(){
	let auth = JSON.parse( localStorage.getItem('auth') )
	if(auth === "" || auth == null){ auth = {handshake:""} }
	return auth
}

$.post(API+'/auth', {auth:getAuth()}, function(data){
	localStorage.setItem( 'auth', JSON.stringify(data) )
})


let store = {
	state:[],
	fn:(action, payload)=>{
		switch(action){

			case "MENU_ON_OFF":
				(payload === "On") ? store.state.menu.img.act = "Off" : store.state.menu.img.act = "On"
				renderAll(store)
				break

			case "MENU_CHG_FORM":
				let inputs = store.state.menu.auth.inputs
				let btns = store.state.menu.auth.btns
				switch(payload){
					case "login":
						for(let i=0; i<inputs.length; i++){
							if( inputs[i].name === "login" || inputs[i].name === "pass" )
							{ inputs[i].act = "y" }else{ inputs[i].act = "n" }
						}
						for(let i=0; i<btns.length; i++){
							if( btns[i].txt === "login"){ btns[i].act = "y" }else{ btns[i].act = "n" }
						}
						break
					case "signin":
						for(let i=0; i<inputs.length; i++){
							if( inputs[i].name === "login" || inputs[i].name === "email" || inputs[i].name === "pass1" || inputs[i].name === "pass2" )
							{ inputs[i].act = "y" }else{ inputs[i].act = "n" }
						}
						for(let i=0; i<btns.length; i++){
							if( btns[i].txt === "signin"){ btns[i].act = "y" }else{ btns[i].act = "n" }
						}
						break
					case "forgot":
						for(let i=0; i<inputs.length; i++){
							if( inputs[i].name === "email" || inputs[i].name === "pass1" || inputs[i].name === "pass2" )
							{ inputs[i].act = "y" }else{ inputs[i].act = "n" }
						}
						for(let i=0; i<btns.length; i++){
							if( btns[i].txt === "forgot"){ btns[i].act = "y" }else{ btns[i].act = "n" }
						}
						break
					default:
						break
				}
				renderAll(store)
				break

			case "GET_DATA_FROM_SERVER":
				let html = "", isObj, isArr, isKey, isVal, isValS, isValE
				let bzJsonParse = (element)=>{
					switch( element ){
						case "{":
							html += `<span style="color:#fd0;">object${ element }</span><div style="margin:0 0 0 2vw;">`
							isObj = true; isArr = false; isKey = true; isVal = false;
							break
						case "}":
							html += `</br></div><span style="color:#fd0;">${ element }</span>`
							isObj = false;
							break
						case "[":
							html += `<span style="color:#1f1;">array${ element }</span><div style="margin:0 0 0 2vw;">`
							isArr = true; isObj = false; isKey = false; isVal = true; isValS = true;
							break
						case "]":
							html += `</br></div><span style="color:#1f1;">${ element }</span>`
							isArr = false; isObj = true;
							break
						case '"':
							if( isKey ){ html += `` }
							if( isVal ){
								if( isValS ){ html += `<span style="color:#f60;">"`; isValS = false; }
								else{ html += `"</span>`; isKey = true; isVal = false; }
							}
							break
						case ":":
							if( isVal ){ html += ` : ` }
							else{ html += ` : `; isKey = false; isVal = true; isValS = true; }
							break
						case ",":
							if( isArr ){ html += `,</br>`; isKey = false; isVal = true; isValS = true; }
							else if( isObj ){ html += `,</br>`; isKey = true; isVal = false; }
							else{ html += `,</br>`; }
							break
						default:
							html += element
							break
					}
				}

				$.get(API+payload.link, function(data){
					let string = JSON.stringify(data)
					for(let i=0; i<string.length; i++){
						bzJsonParse(string[i])
					}
					store.state.queryHtml = {
						query:API+payload.link,
						html:html
					}
					renderAll(store)
				})
				break

			default:
				break

		}
	}
}


$.get(API+'/projects', function(data){
	store.state.bzProjects = data
})
.done(function(){
	renderAll(store)
})
$.get(API+'/menu', function(data){
	store.state.menu = data
})
.done(function(){
	renderAll(store)
})
$.get(API+'/copyright', function(data){
	store.state.copyright = data
})
.done(function(){
	renderAll(store)
})

export default store